    userHAveActivePackage(userPackageDetailDtoList){
      let hasActivePackage=false;
      if(userPackageDetailDtoList&&userPackageDetailDtoList.length>0){
      
      userPackageDetailDtoList.forEach((ele:any) => {
        if(ele.status=='ACTIVE'){
          hasActivePackage=true;
        }
      });
    }
      return hasActivePackage;
    }

    isUserExistOnOtherPage(userId:any){
      let userExist=true;
      for (let index = this.currentPage-1; index == 0; index--) {
        
        if(index!=this.currentPage){
          let users= this.userListPageWise.get(index)
          users.forEach(element => {
            if(element==userId){
              userExist=false;

            }
            
          });

        }
        
      }
      return userExist;

    }
