    applySort(item){
      this.headerTitleGYMRegular.forEach(element => {
        if(item.id!=element.id){
          element.sortapplied=false;

        }
        else{
          element.sortapplied=true;
          if(element.sortType=='ASC'){
            element.sortType = 'DESC'
          }else{
            element.sortType = 'ASC'

          }

        }
        
      });
      this.sortList();
    }


    sortList(){
      this.headerTitleGYMRegular.forEach(element => {
        if(element.sortapplied){
          if(element.id==111){
            if(element.sortType=="DESC"){
              this.customersData.sort(function(a,b){return b.balClass - a.balClass})

            }else{
              this.customersData.sort(function(a,b){return a.balClass - b.balClass})

            }
          }
          if(element.id==222){
            if(element.sortType=="DESC"){
              this.customersData.sort(function(a,b){return b.noOfTransaction - a.noOfTransaction})

            }else{
              this.customersData.sort(function(a,b){return a.noOfTransaction - b.noOfTransaction})

            }

          }
          if(element.id==333){
            if(element.sortType=="DESC"){
              this.customersData.sort(function(a,b){return b.noOfClassJoin - a.noOfClassJoin})

            }else{
              this.customersData.sort(function(a,b){return a.noOfClassJoin - b.noOfClassJoin})

            }

          }
        
        
        }
        
        
      });
    }
